import { Capacitor } from '@capacitor/core'
import { Filesystem } from '@capacitor/filesystem'
import { IonAlert, IonSpinner } from '@ionic/react'
import imageCompression from 'browser-image-compression'
import { observer } from 'mobx-react-lite'
import React, { useState } from 'react'
import * as FIcon from 'react-feather'
import { TiCameraOutline } from 'react-icons/ti'
import { RouteComponentProps } from 'react-router'

import { AdminItemFieldRegistro, FooterFormulario, PageApp, SelectorModalRegistro } from '../../components'
import PhotoLoaderModalRegistroMultiple from '../../components/PhotoLoaderModalRegistroMultiple'
import { SubHeader } from '../../components/SubHeader'
import { RUTAS_APP } from '../../constants'
import { comprimirImagen, useToaster, useTryGoBack } from '../../helpers-ts'
import { useStores } from '../../models/root-store/root-store-context'
import './VehiculoTransportistaCreate.css'

const defaultValues = {
  tipo_vehiculo: '',
  marca: '',
  modelo: '',
  ano: '',
  numero_chapa: '',
  color: '',
  kilometraje: '',
  numero_chassis: '',
  habilitacion_frente: '',
  habilitacion_dorso: '',
  poliza_seguro: '',
  habilitacion_vencimiento: '',
}

interface previews {
  habilitacion_frente: string | null
  habilitacion_dorso: string | null
  poliza_seguro: string | null
}

const defaultPreviews = {
  habilitacion_frente: null,
  habilitacion_dorso: null,
  poliza_seguro: null,
}

export const VehiculoTransportistaCreate: React.FC<RouteComponentProps> = observer(({ history }) => {
  const rootStore = useStores()
  const { transportistaStore } = rootStore
  const [Alert, setAlert] = useState(false)
  const [TextAlert, setTextAlert] = useState('')
  const [TitleAlert, setTitleAlert] = useState('')
  const [Errors, setErrors] = useState(defaultValues)
  const [Datos, setDatos] = useState(defaultValues)
  const [Previews, setPreviews] = useState<previews>(defaultPreviews)
  const [Loading, setLoading] = useState(false)
  const tryGoBack = useTryGoBack()
  const addToast = useToaster()

  const alertAccion = (accion: string) => {
    setAlert(true)
    setTitleAlert('Crear vehículo')
    setTextAlert('Confirma que desea crear el vehículo?')
  }

  const alertEjecutarAccion = () => {
    guardar()
  }

  const setDato = (campo: string, valor: any) => {
    let datos = { ...Datos }
    // @ts-ignore
    datos[campo] = valor
    setDatos(datos)
  }

  const setPreview = (campo: string, valor: string) => {
    const previews = { ...Previews }
    //@ts-ignore
    previews[campo] = valor
    setPreviews(previews)
  }

  const getDato = (campo: string) => {
    let datos = { ...Datos }
    // @ts-ignore
    return datos[campo]
  }

  const getError = (campo: string) => {
    let error = { ...Errors }
    // @ts-ignore
    return error[campo]
  }

  const dataSelectorTipoVehiculoModal = (data: any) => {
    setDato('tipo_vehiculo', data[0])
  }

  const imgSelected = async (data: any, tipo: string) => {
    if (Capacitor.getPlatform() !== 'web') {
      const contents = await Filesystem.readFile({
        path: data,
      })
      data = `data:image/jpeg;base64,${contents.data}`
    }
    let blob = await fetch(data).then((r) => r.blob())
    const imagen_comprimida = await comprimirImagen(blob, tipo)
    const imagen_base64 = await imageCompression.getDataUrlFromFile(imagen_comprimida)
    setDato(tipo, imagen_comprimida)
    setPreview(tipo, imagen_base64)
  }

  const guardar = () => {
    setLoading(true)
    setErrors(defaultValues)
    // @ts-ignore
    const datos = { ...Datos, transportista: transportistaStore.transportista.id }
    rootStore.environment.api.guardarVehiculo(datos, true).then((result: any) => {
      setLoading(false)
      if (result.kind === 'ok') {
        history.replace(RUTAS_APP.VEHICULOS)
      } else if (result.kind === 'bad-data') {
        addToast('Corrija los errores y vuelva a intentar', 'warning')
        setErrors(result.errors)
        if (result.errors.non_field_errors) {
          alert(result.errors.non_field_errors[0])
        }
      } else {
        alert('Ocurrió un error al guardar vuelva a intentar en unos momentos')
      }
    })
  }

  return (
    <PageApp
      pageStyles={'transportista-page background-blanco'}
      titulo_header={'Nuevo vehículo'}
      accion_boton_derecho={() => tryGoBack(RUTAS_APP.VEHICULOS)}
      boton_derecho={<FIcon.X size={30} color={'white'} />}
      sub_titulo={` `}
      mostrar_barra={false}
    >
      <IonAlert
        isOpen={Alert}
        onDidDismiss={() => setAlert(false)}
        header={TitleAlert}
        message={TextAlert}
        buttons={[
          {
            text: 'Cancelar',
            role: 'cancel',
            cssClass: 'cancel-button',
          },
          {
            text: 'Confirmar',
            cssClass: 'confirm-button-transportista',
            handler: () => alertEjecutarAccion(),
          },
        ]}
      />
      <SubHeader
        links={[
          { titulo: 'Perfil', ruta: RUTAS_APP.PERFIL },
          { titulo: 'Configuración', ruta: RUTAS_APP.CONFIGURACION },
          { titulo: 'Rodados', ruta: RUTAS_APP.VEHICULOS },
          { titulo: 'Añadir' },
        ]}
      />
      <div id="datos-nuevo-vehiculo" className={'vehiculo-transportista-list-container py-2'}>
        <h4 style={{ textAlign: 'center' }}>Añadir</h4>
        <div style={{ display: 'flex', justifyContent: 'center' }}>
          <img id="icono-auto" src={'assets/icon/car.png'} />
        </div>

        <div className={'row'}>
          <div className={'col-12'}>
            <SelectorModalRegistro
              titulo={'Tipo de Rodado'}
              titulo_label={'Tipo de Rodado'}
              listado_elementos={rootStore.tiposVehiculos}
              // @ts-ignore
              listado_elementos_seleccionados={[]}
              datos_filtrados={dataSelectorTipoVehiculoModal}
              multiple={false}
              llave_label_opcion={'nombre'}
              class_boton_aceptar={'btn-outline-orange'}
              checkbox_style={'checkbox-custom-orange'}
            />
            {Errors.tipo_vehiculo && <div className={'text-danger'}>{Errors.tipo_vehiculo}</div>}
          </div>
          <AdminItemFieldRegistro
            titulo={'Marca'}
            tipo={'texto'}
            campo={'marca'}
            valor={Datos ? Datos.marca : ''}
            set_dato={setDato}
            columnas={12}
            error={Errors && Errors.marca}
          />

          <AdminItemFieldRegistro
            titulo={'Modelo'}
            tipo={'texto'}
            campo={'modelo'}
            valor={Datos ? Datos.modelo : ''}
            set_dato={setDato}
            columnas={12}
            error={Errors && Errors.modelo}
          />
          <div className="fila-container">
            <AdminItemFieldRegistro
              titulo={'Año'}
              tipo={'texto'}
              campo={'ano'}
              valor={Datos ? Datos.ano : ''}
              set_dato={setDato}
              columnas={12}
              columnas_sm={12}
              error={Errors && Errors.ano}
            />
            <AdminItemFieldRegistro
              titulo={'Color'}
              tipo={'texto'}
              campo={'color'}
              valor={Datos ? Datos.color : ''}
              set_dato={setDato}
              columnas={12}
              columnas_sm={12}
              error={Errors && Errors.color}
            />
            <AdminItemFieldRegistro
              titulo={'Kilometraje'}
              tipo={'numero'}
              campo={'kilometraje'}
              valor={Datos ? Datos.kilometraje : ''}
              set_dato={setDato}
              columnas={12}
              columnas_sm={12}
              error={Errors && Errors.kilometraje}
            />
            <AdminItemFieldRegistro
              titulo={'Chapa'}
              tipo={'texto'}
              campo={'numero_chapa'}
              valor={Datos ? Datos.numero_chapa : ''}
              set_dato={setDato}
              columnas={12}
              columnas_sm={12}
              error={Errors && Errors.numero_chapa}
            />
          </div>
          <AdminItemFieldRegistro
            titulo={'Número de Chasis'}
            tipo={'texto'}
            campo={'numero_chassis'}
            valor={Datos ? Datos.numero_chassis : ''}
            set_dato={setDato}
            columnas={12}
            columnas_sm={12}
            error={Errors && Errors.numero_chassis}
          />
        </div>
        <PhotoLoaderModalRegistroMultiple
          perfil={false}
          titulo_modal={'Póliza de seguro del rodado'}
          titulo_label={
            <>
              <TiCameraOutline size={20} />
              <span>Subir foto de la Póliza de seguro</span>
            </>
          }
          helptext={''}
          infoBody={'Subí una foto de la póliza de seguro al día.'}
          infoTitulo="Foto de Póliza de seguro"
          img_path={'assets/document.png'}
          on_select={imgSelected}
          imageItems={[
            {
              titulo: 'Poliza de seguro',
              userDataLabel: 'poliza_seguro',
              preview: Previews.poliza_seguro ? Previews.poliza_seguro : null,
            },
          ]}
          color={'#ff6f33'}
          class_boton_aceptar={'btn-outline-orange'}
        />
        {Errors.poliza_seguro && <div className={'text-danger'}>{Errors.poliza_seguro}</div>}
        <PhotoLoaderModalRegistroMultiple
          titulo_label={
            <>
              <TiCameraOutline size={20} />
              <span>Subir foto de la Habilitación</span>
            </>
          }
          titulo_modal={'Subir foto de la habilitación'}
          helptext={''}
          infoBody={'Subí una foto del frente y dorso de tu habilitacion al día.'}
          infoTitulo="Foto de habilitación"
          modalInfoCssClass="info-modal-2lines"
          img_path={'assets/id.png'}
          on_select={imgSelected}
          imageItems={[
            {
              titulo: 'Habilitacion de rodado (frente)',
              userDataLabel: 'habilitacion_frente',
              preview: Previews.habilitacion_frente ? Previews.habilitacion_frente : null,
            },
            {
              titulo: 'Habilitacion de rodado (dorso)',
              userDataLabel: 'habilitacion_dorso',
              preview: Previews.habilitacion_dorso ? Previews.habilitacion_dorso : null,
            },
          ]}
          perfil={false}
          color={'#ff6f33'}
          class_boton_aceptar={'btn-outline-orange'}
        />
        {Errors.habilitacion_dorso && <div className={'text-danger mb-2'}>{Errors.habilitacion_dorso}</div>}
        {Errors.habilitacion_frente && <div className={'text-danger mb-2'}>{Errors.habilitacion_frente}</div>}
        <div className="fila-container">
          <AdminItemFieldRegistro
            titulo={'22/02/22'}
            titulo_externo={'Fecha de vencimiento'}
            tipo={'fecha'}
            campo={'habilitacion_vencimiento'}
            valor={Datos ? Datos.habilitacion_vencimiento : ''}
            set_dato={setDato}
            columnas={12}
            columnas_sm={6}
            error={Errors && Errors.habilitacion_vencimiento}
            info_internal_icon={<img src="assets/icon/calendar.png" />}
          />
        </div>
      </div>
      <FooterFormulario className="py-4 px-5">
        <div />
        <button className="btn btn-principal-transportista" disabled={Loading} onClick={() => alertAccion('crear')}>
          {!Loading && <span>Guardar</span>}
          {Loading && <IonSpinner />}
        </button>
        </FooterFormulario>
    </PageApp>
  )
})

export default VehiculoTransportistaCreate
